import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow,
  CFormGroup,
  CLabel,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import Background from "../../../assets/images/background.jpg";
import api from "../../../api";
import Toast from "light-toast";
import md5 from "md5";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 44%;
  top: 50%;
  z-index: 1;
  position: fixed;
`;

class ForgotPassword extends Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.generateOTP = this.generateOTP.bind(this);
    this.verifyOTP = this.verifyOTP.bind(this);
    this.updatePassword = this.updatePassword.bind(this);

    this.state = {
      phone: "",
      otp: null,
      serverOtp: null,

      generateOTPError: "",
      verifyOTPError: "",

      userId: null,
      email: "",
      confirmPassword: "",
      password: "",

      passwordError: "",

      //spinner
      loading: false,
    };
  }

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
    this.setState({
      generateOTPError: "",
    });
    this.setState({
      verifyOTPError: "",
    });
    this.setState({
      passwordError: "",
    });
  };

  generateOTP = (e) => {
    if (this.state.phone === "") {
      this.setState({ generateOTPError: "Enter phone no.!!!" });
    }

    if (this.state.phone !== "") {
      var login_data = {
        phone: this.state.phone,
      };

      this.setState({ loading: true });
      fetch(api.apiUrl + "generate_otp", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        // headers: authHeader(),
        body: JSON.stringify(login_data),
      })
        .then((response) => response.json())
        .then((responseJsonFromServerData) => {
          if (responseJsonFromServerData.status === "success") {
            this.setState({
              loading: false,
              serverOtp: responseJsonFromServerData.serverOtp,
              email: responseJsonFromServerData.email,
              userId: responseJsonFromServerData.userId,
            });
            document.getElementById("generate_otp_form").style.display = "none";
            document.getElementById("verify_otp_form").style.display = "block";
          } else if (responseJsonFromServerData.status === "failed") {
            this.setState({
              generateOTPError: "Phone no. not found!",
              loading: false,
            });
          }
        })
        .catch((error) => {
          console.error(error);
          this.setState({ loading: false });
        });
    }
  };

  verifyOTP = () => {
    this.setState({ loading: true });

    if (this.state.serverOtp === parseInt(this.state.otp)) {
      this.setState({
        loading: false,
      });
      document.getElementById("verify_otp_form").style.display = "none";
      document.getElementById("password_reset").style.display = "block";
    } else {
      this.setState({
        verifyOTPError: "OTP is not valid!",
        loading: false,
      });
    }
  };

  updatePassword = () => {
    if (this.state.password === "") {
      this.setState({ passwordError: "Enter Password" });
    }
    if (this.state.confirmPassword === "") {
      this.setState({ passwordError: "Enter Confirm Password" });
    }

    if (this.state.confirmPassword === "" && this.state.password === "") {
      this.setState({ passwordError: "Enter Password" });
    }

    if (
      this.state.confirmPassword !== "" &&
      this.state.password !== "" &&
      this.state.confirmPassword.length < 8
    ) {
      this.setState({ passwordError: "Minimun 8 char Password" });
    }

    if (
      this.state.confirmPassword !== "" &&
      this.state.password !== "" &&
      this.state.confirmPassword !== this.state.password
    ) {
      this.setState({ passwordError: "Password not matching" });
    }

    if (
      this.state.confirmPassword !== "" &&
      this.state.password !== "" &&
      this.state.confirmPassword.length >= 8 &&
      this.state.confirmPassword === this.state.password
    ) {
      var profile_data = {
        id: this.state.userId,
        password: md5(this.state.password),
      };
      this.setState({ loading: true });
      fetch(api.apiUrl + "reset_password", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(profile_data),
      })
        .then((response) => response.json())
        .then((responseJsonFromServerData) => {
          if (responseJsonFromServerData === "success") {
            Toast.success("Password reset successfully...", 3000, () => {
              this.props.history.push("/login");
            });
          } else if (responseJsonFromServerData === "failed") {
            Toast.fail("failed...", 3000, () => {
              // do something after the toast disappears
            });
          }
          this.setState({ loading: false, password: "", confirmPassword: "" });
        })
        .catch((error) => {
          console.error(error);
          this.setState({ loading: false });
        });
    }
  };

  render() {
    return (
      <div>
        <ScaleLoader
          height={40}
          width={12}
          css={override}
          size={150}
          //size={"150px"} this also works
          color={"#B54545"}
          loading={this.state.loading}
        />
        <div
          className="c-app c-default-layout flex-row align-items-center"
          style={{
            backgroundImage: `url(${Background})`,
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
          }}
        >
          <CContainer>
            <CRow className="justify-content-center">
              <CCol md="6">
                <CCardGroup>
                  <CCard className="p-4">
                    <CCardBody>
                      {/* generate otp */}
                      <CForm id="generate_otp_form">
                        <h2>Forgot Passord</h2>
                        <p className="text-muted">
                          Enter your phone number to reset the password.{" "}
                        </p>
                        <CInputGroup className="mb-3">
                          <CInputGroupPrepend>
                            <CInputGroupText>
                              <CIcon name="cil-phone" />
                            </CInputGroupText>
                          </CInputGroupPrepend>
                          <CInput
                            autoFocus
                            type="text"
                            placeholder="Enter Phone No."
                            autoComplete="phone"
                            onChange={this.handleChange("phone")}
                            style={{
                              borderBottomColor:
                                this.state.generateOTPError !== "" ? "red" : "",
                              borderTopColor:
                                this.state.generateOTPError !== "" ? "red" : "",
                              borderRightColor:
                                this.state.generateOTPError !== "" ? "red" : "",
                            }}
                          />
                        </CInputGroup>

                        <p
                          className="error"
                          style={{
                            display:
                              this.state.generateOTPError === ""
                                ? "none"
                                : "block",
                          }}
                        >
                          <i className="fa fa-exclamation-triangle"></i>{" "}
                          {this.state.generateOTPError}
                        </p>
                        <CRow>
                          <CCol sm="12" md="12" xs="12">
                            <CButton
                              className="px-4 main-btn"
                              onClick={this.generateOTP}
                            >
                              Generate OTP
                            </CButton>
                            <Link to="/login">
                              <CButton className="px-4 main-cl">Cancel</CButton>
                            </Link>
                          </CCol>
                        </CRow>
                      </CForm>

                      {/* verify otp */}
                      <CForm id="verify_otp_form" style={{ display: "none" }}>
                        <h2>Forgot Passord</h2>
                        <p className="text-muted">
                          Enter OTP generated on your phone number.{" "}
                        </p>
                        <CInputGroup className="mb-3">
                          <CInputGroupPrepend>
                            <CInputGroupText>
                              <CIcon name="cil-phone" />
                            </CInputGroupText>
                          </CInputGroupPrepend>
                          <CInput
                            autoFocus
                            type="text"
                            placeholder="Enter OTP"
                            onChange={this.handleChange("otp")}
                            style={{
                              borderBottomColor:
                                this.state.verifyOTPError !== "" ? "red" : "",
                              borderTopColor:
                                this.state.verifyOTPError !== "" ? "red" : "",
                              borderRightColor:
                                this.state.verifyOTPError !== "" ? "red" : "",
                            }}
                          />
                        </CInputGroup>

                        <p
                          className="error"
                          style={{
                            display:
                              this.state.verifyOTPError === ""
                                ? "none"
                                : "block",
                          }}
                        >
                          <i className="fa fa-exclamation-triangle"></i>{" "}
                          {this.state.verifyOTPError}
                        </p>
                        <CRow>
                          <CCol sm="6" md="6" xs="12">
                            <CButton
                              className="px-4 main-btn"
                              onClick={this.verifyOTP}
                            >
                              Verify OTP
                            </CButton>
                          </CCol>
                        </CRow>
                      </CForm>
                      {/* Update password */}
                      <CForm id="password_reset" style={{ display: "none" }}>
                        <CFormGroup>
                          <CLabel htmlFor="email">Email</CLabel>
                          <CInput
                            autoFocus
                            id="email"
                            placeholder=""
                            defaultValue={this.state.email}
                            required
                            disabled
                          />
                        </CFormGroup>
                        <CFormGroup>
                          <CLabel htmlFor="pass">Password</CLabel>
                          <CInput
                            id="pass"
                            type="password"
                            placeholder="Enter your name"
                            onChange={this.handleChange("password")}
                            style={{
                              borderColor:
                                this.state.passwordError === "" ? "" : "red",
                            }}
                            required
                          />
                        </CFormGroup>
                        <CFormGroup>
                          <CLabel htmlFor="confirmPass">
                            Confirm Password
                          </CLabel>
                          <CInput
                            id="confirmPass"
                            type="password"
                            placeholder="Enter your name"
                            onChange={this.handleChange("confirmPassword")}
                            style={{
                              borderColor:
                                this.state.passwordError === "" ? "" : "red",
                            }}
                            required
                          />
                        </CFormGroup>
                        <p
                          className="error"
                          style={{
                            display:
                              this.state.passwordError === ""
                                ? "none"
                                : "block",
                          }}
                        >
                          <i className="fa fa-exclamation-triangle"></i>{" "}
                          {this.state.passwordError}
                        </p>
                        <CCol md="12" sm="12" xs="12" className="text-center">
                          <CButton
                            className="px-4 submit-btn"
                            onClick={this.updatePassword}
                          >
                            Submit
                          </CButton>
                          <CButton className="px-4 cancel-btn" to="/login">
                            Cancel
                          </CButton>
                        </CCol>
                      </CForm>
                    </CCardBody>
                  </CCard>
                </CCardGroup>
              </CCol>
            </CRow>
          </CContainer>
        </div>
      </div>
    );
  }
}

export default ForgotPassword;
